import { Component } from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {AuthenticationProvider} from "../../providers/authentication/authentication";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";

/**
 * Generated class for the ChangePin01Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-change-pin01',
  templateUrl: 'change-pin01.html',
})
export class ChangePin01Page {

  passPolicy;
  changeTPIN01Error = "";
  formVal = {authCode: "", currentTPIN: "",newTPIN: "",confirmTPIN: ""};

  authCode;
  currentTPIN;
  newTPIN;
  confirmTPIN;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public authenticationService: AuthenticationProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public tokenService : TokenServiceProvider,
              public gaProvider : GoogleAnalyticsProvider) {
  }

  ChangeTPIN01Action = function () {

    this.changeTPIN01Error = "";
    this.authCode = this.formVal.authCode;
    this.currentTPIN =this.formVal.currentTPIN;
    this.newTPIN = this.formVal.newTPIN;
    this.confirmTPIN = this.formVal.confirmTPIN;

    let flag = true;

    if (this.authCode == "" || typeof(this.authCode) == "undefined")
    {
      this.changeTPIN01Error = "Auth Code is required.";
      flag = false;
      return;
    }

    if (this.currentTPIN == "" || typeof(this.currentTPIN) == "undefined")
    {
      this.changeTPIN01Error = "Current TPIN is required.";
      flag = false;
      return;
    }
    if (this.newTPIN == "" || typeof(this.newTPIN) == "undefined")
    {
      this.changeTPIN01Error = "New TPIN is required.";
      flag = false;
      return;
    }

    if (this.confirmTPIN == "" || typeof(this.confirmTPIN) == "undefined")
    {
      this.changeTPIN01Error = "Confirm TPIN is required.";
      flag = false;
      return;

    }

    if (!(this.newTPIN == this.confirmTPIN))
    {
      this.changeTPIN01Error = "New TPIN and Confirm new TPIN Mismatch.";
      flag = false;
      this.formVal = {authCode: "", currentTPIN: "",newTPIN: "",confirmTPIN: ""};
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encAuthCode = this.cryptoUtil.getEncryptVal(this.authCode);
      let encCurrentPin = this.cryptoUtil.getEncryptVal(this.currentTPIN);
      let encNewPin = this.cryptoUtil.getEncryptVal(this.newTPIN);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.authenticationService.changeTpin(unMaskToken, appData.userId, appData.appId, encAuthCode, encCurrentPin,encNewPin)
        .then(data => {
          this.onChangeTPINComplete(data);
        });
    }
  };

  onChangeTPINComplete = function (data) {
    if (data.statusCode == 1) {
      this.navCtrl.push('ChangePin02Page', {});
    }

    else if (data.statusCode == 0) {
      this.changeTPIN01Error = data.note;
      this.formVal = {authCode: "", currentTPIN: "",newTPIN: "",confirmTPIN: ""};
    }

    else {
      this.changeTPIN01Error = "System Error Contact with Administrator.";
    }
  };


  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Change TPIN");
  }

}



